export default {
	data() {
		return {
			userName: '许德琰测试账号',
			articleTitle: '',
			articleContent: '',
			articleTag: '',
			imgPath: '',
			showInputTagArea: 0,
			showAddTagButton: 1,
			showTagArea: 0,
			tagList: [],
			tagIndex: 0
		}
	},
	onLoad() {

	},
	methods: {
		// 将标题存放在articleTitle中
		saveAsArticleTitle: function(event) {
			this.articleTitle = event.target.value;
			// console.log(this.title);
		},
		// 将内容存放在articleContent中
		saveAsArticleContent: function(event) {
			this.articleContent = event.target.value;
			// console.log(this.content);
		},
		addTag: function(res) {
			this.showInputTagArea = 1;
			this.showAddTagButton = 0;
		},
		checkInput: function(res) {
			var that = this;
			var tag = res.target.value;
			if (this.isNull(tag)) {
				that.showAddTagButton = 1;
				that.showInputTagArea = 0;
			} else {
				that.showTagArea = 1;
				that.tagList[that.tagIndex] = tag;
				that.tagIndex = that.tagIndex + 1;
				that.showAddTagButton = 1;
				that.showInputTagArea = 0;
			}
		},
		chooseImg: function() {
			var that = this;
			var path = '';
			uni.chooseImage({
				count: 1,
				sizeType: ['compressed'],
				success: function(res) {
					var tempFilePaths = res.tempFilePaths;
					that.imgPath = tempFilePaths;
				}
			})
		},
		upload: function(e) {
			var me = this;

			console.log(me.articleContent);
			console.log(me.articleTitle);
			console.log(me.imgPath);

			var serverUrl = me.SeverUrl;
			uni.uploadFile({
				url: serverUrl + '/upload',
				filePath: me.imgPath[0],
				name: 'file',
				formData: {
					userId: me.userName,
					articleTitle: me.articleTitle,
					articleContent: me.articleContent
				},
				success: res => {
					uni.redirectTo({
						url: '../index/index',
					});
				}
			});
		}
	}
};
